<template>
    <div id="overview">
        <section class="overview-panel-widget">
            <el-row :gutter="10">
                <el-col :xs="24" :sm="4" :md="4" :lg="4" v-for="item in panelWidget" :key="item.name">
                    <div class="panel panel-widget" v-bind:class="item.colors">
                        <el-row >
                            <el-col :span="8">
                                <div class="widget-left"><i v-bind:class="item.fontAwesome"></i></div>
                            </el-col>
                            <el-col :span="16">
                                <div class="widget-right">
                                    <div class="large">{{item.value}}</div>
                                    <div class="text-muted">{{item.name}}</div>
                                </div>
                            </el-col>
                        </el-row>
                    </div>
                </el-col>
            </el-row>
            <hr>
            <el-row :gutter="10">
                 <el-col :xs="24" :sm="24" :md="12" :lg="12">
                    <div style="background-color:#ffffff;">
                      <div class="chart" id="cpu-chart" style="width: 100%; height:325px;"></div>
                    </div>
                 </el-col>
                 <el-col :xs="24" :sm="12" :md="6" :lg="6">
                     <el-table :data="clusterInfo" height="325" border style="width: 100%">
                        <el-table-column prop="name" label="Cluster Health" width="200"> </el-table-column>
                        <el-table-column prop="value"> </el-table-column>
                    </el-table>
                 </el-col>
                  <el-col :xs="24" :sm="12" :md="6" :lg="6">
                     <el-table :data="statsInfo" height="325" border style="width: 100%">
                        <el-table-column prop="name" label="ElasticSearch Stats Info" width="200"> </el-table-column>
                        <el-table-column prop="value"> </el-table-column>
                    </el-table>
                 </el-col>
            </el-row>
            <hr>
            <el-row :gutter="10">
                 <el-col :xs="24" :sm="24" :md="12" :lg="12">
                    <div style="background-color:#ffffff;">
                      <div class="chart" id="temp-chart" style="width: 100%; height:350px;"></div>
                    </div>
                 </el-col>
                 <el-col :xs="24" :sm="24" :md="12" :lg="12">
                    <el-table :data="indexList.slice(0, 6)" border style="width: 100%" height="350">
                        <el-table-column label="Index list" >
                            <el-table-column type="expand">
                                <template scope="props">
                                    <el-form label-position="left" inline class="demo-table-expand" >
                                    <el-form-item label="">
                                        <span>{{ props.row.index }}</span>
                                    </el-form-item>
                                    <el-form-item label="">
                                        <span>{{ props.row.docs }}</span>
                                    </el-form-item>
                                     <el-form-item label="">
                                        <span>{{ props.row.delete }}</span>
                                    </el-form-item>
                                    <el-form-item label="STORE SIZE">
                                        <span>{{ props.row.size }}</span>
                                    </el-form-item>
                                     <el-form-item label="">
                                        <span>{{ props.row.prisize }}</span>
                                    </el-form-item>
                                    <el-form-item label="">
                                        <span>{{ props.row.pri }}</span>
                                    </el-form-item>
                                    <el-form-item label="">
                                        <span>{{ props.row.rep }}</span>
                                    </el-form-item>
                                    <el-form-item label="">
                                        <span>{{ props.row.status }}</span>
                                    </el-form-item>
                                    <el-form-item label="">
                                        <span>{{ props.row.health }}</span>
                                    </el-form-item>
                                    <el-form-item label="UUID">
                                        <span>{{ props.row.uuid }}</span>
                                    </el-form-item>
                                    </el-form>
                                </template>
                            </el-table-column>
                            <el-table-column prop="index" label="Index" width="220"></el-table-column>
                            <el-table-column prop="docs" label="Docs" sortable width="100"></el-table-column>
                            <el-table-column prop="size" label="Primary Size" sortable></el-table-column>
                            <el-table-column prop="status" label="Status"></el-table-column>
                            <el-table-column label="">
                            <template scope="scope">
                                <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
                            </template>
                            </el-table-column>
                        </el-table-column>
                    </el-table>
                 </el-col>
            </el-row>
        </section>
    </div>
</template>

<script>
import echarts from 'echarts'
import { mapGetters } from 'vuex'
export default {
  name: 'overview',
  data: function () {
    return {

    }
  },
  computed: {
    ...mapGetters([
      'panelWidget',
      'statsInfo',
      'clusterInfo',
      'health',
      'serverhost',
      'charts',
      'templateInfo',
      'indexList'
    ])
  },
  created: function () {
    this.$store.dispatch('SetClusterInfo', this.health)
    this.$store.dispatch('SetStats', {url: '/_cluster/stats', host: this.serverhost})
    this.$store.dispatch('GetTemplateInfo', {url: '/_template', host: this.serverhost})
  },
  watch: {
    health (val, oldval) {
      this.$store.dispatch('SetClusterInfo', val)
    },
    charts (val, oldval) {
      echarts.init(document.getElementById('cpu-chart')).setOption(this.circle(this.charts))
    },
    templateInfo (val, oldval) {
      echarts.init(document.getElementById('temp-chart')).setOption(this.tempChart(this.templateInfo))
    }
  },
  mounted: function () {
    echarts.init(document.getElementById('cpu-chart')).setOption(this.circle(this.charts))
    echarts.init(document.getElementById('temp-chart')).setOption(this.tempChart(this.templateInfo))
  },
  methods: {
    circle: function (charts) {
      var labelFromatter = {
        normal: {
          label: {
            formatter: function (params) {
              //  console.log(params.name)
              var gb = 1024 * 1024 * 1024
              var mb = 1024 * 1024
              var kb = 1024
              if (params.value > gb) {
                return Math.ceil(params.value / gb) + 'GB'
              }
              if (params.value > mb) {
                return Math.ceil(params.value / mb) + 'MB'
              }
              if (params.value > kb) {
                return Math.ceil(params.value / kb) + 'KB'
              }
              return params.value
            },
            textStyle: {
              baseline: 'top'
            }
          }
        }
      }
      var labelBottom = {
        normal: {
          color: '#ccc',
          label: {
            show: true,
            position: 'center'
          },
          labelLine: {
            show: false
          }
        }
      }
      var labelTop = {
        normal: {
          label: {
            show: true,
            position: 'center',
            formatter: '{b}',
            textStyle: {
              baseline: 'bottom'
            }
          },
          labelLine: {
            show: false
          }
        }
      }
      function b2b (value) {
        //  console.log(params.name)
        var gb = 1024 * 1024 * 1024
        var mb = 1024 * 1024
        var kb = 1024
        if (value > gb) {
          return Math.ceil(value / gb) + 'GB'
        }
        if (value > mb) {
          return Math.ceil(value / mb) + 'MB'
        }
        if (value > kb) {
          return Math.ceil(value / kb) + 'KB'
        }
        return value + 'B'
      }
      var option = {
        tooltip: {
          trigger: 'item',
          formatter: function (params) {
            if (params.name === 'Jvm') {
              return 'JVM: ' + b2b(params.value)
            }
            if (params.name === 'Jvm-mem') {
              return 'JVM: ' + b2b(charts.memtotal - charts.memused)
            }
            if (params.name === 'Mem') {
              return ': ' + b2b(charts.memused)
            }
            if (params.name === 'Fs-total') {
              return ': ' + b2b(charts.fsfree)
            }
            if (params.name === 'Fs') {
              return ': ' + b2b(charts.fstotal - charts.fsfree)
            }
            if (params.name === 'FieldData') {
              return 'Fielddata: ' + b2b(charts.fielddata_mem_used)
            }
            if (params.name === 'QueryCache') {
              return 'QueryCache: ' + b2b(charts.query_cache_memory_size)
            }
            if (params.name === 'QueryCache-mem' || params.name === 'Field-mem' || params.name === 'Mem-total') {
              return ': ' + b2b(charts.memtotal - charts.memused)
            }
            return params.name + ':' + b2b(params.value)
          }
        },
        legend: {
          x: 'center',
          y: 'bottom',
          data: [
            'Jvm', 'Mem', 'Fs', 'FieldData', 'QueryCache'
          ]
        },
        title: {
          text: 'ElasticSearch Cluster Stats',
          subtext: ',,JVM',
          x: 'center'
        },
        series: [
          {
            type: 'pie',
            center: ['13%', '50%'],
            radius: [40, 55],
            x: '0%',
            itemStyle: labelFromatter,
            data: [
              {name: 'Jvm-mem', value: charts.memfree, itemStyle: labelBottom},
              {name: 'Jvm', value: charts.vm_mem_used, itemStyle: labelTop}
            ]
          },
          {
            type: 'pie',
            center: ['31%', '50%'],
            radius: [40, 55],
            x: '0%',
            itemStyle: labelFromatter,
            data: [
              {name: 'Mem-total', value: charts.memfree, itemStyle: labelBottom},
              {name: 'Mem', value: charts.memused, itemStyle: labelTop}
            ]
          },
          {
            type: 'pie',
            center: ['49%', '50%'],
            radius: [40, 55],
            x: '0%',
            itemStyle: labelFromatter,
            data: [
              {name: 'Fs-total', value: charts.fsfree, itemStyle: labelBottom},
              {name: 'Fs', value: charts.fstotal - charts.fsfree, itemStyle: labelTop}
            ]
          },
          {
            type: 'pie',
            center: ['67%', '50%'],
            radius: [40, 55],
            x: '0%',
            itemStyle: labelFromatter,
            data: [
              {name: 'Field-mem', value: charts.memfree, itemStyle: labelBottom},
              {name: 'FieldData', value: charts.fielddata_mem_used, itemStyle: labelTop}
            ]
          },
          {
            type: 'pie',
            center: ['85%', '50%'],
            radius: [40, 55],
            x: '0%',
            itemStyle: labelFromatter,
            data: [
              {name: 'QueryCache-mem', value: charts.memfree, itemStyle: labelBottom},
              {name: 'QueryCache', value: charts.query_cache_memory_size, itemStyle: labelTop}
            ]
          }
        ]
      }
      return option
    },
    tempChart: function (templateInfo) {
      var tempOption = {
        title: {
          text: 'Type',
          subtext: '',
          x: 'center'
        },
        color: ['#3398DB'],
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [{
          type: 'category',
          data: templateInfo.templateName,
          axisTick: {
            alignWithLabel: true
          }
        }],
        yAxis: [{
          axisTick: {
            alignWithLabel: true
          }
        }],
        series: [{
          name: '',
          type: 'bar',
          barWidth: '40%',
          data: templateInfo.typeCount
        }],
        label: {
          normal: {
            show: true,
            position: 'top',
            formatter: '{c}'
          }
        },
        itemStyle: {
          normal: {
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
              offset: 0,
              color: 'rgba(17, 168,171, 1)'
            },
            {
              offset: 1,
              color: 'rgba(17, 168,171, 0.1)'
            }]),
            shadowColor: 'rgba(0, 0, 0, 0.1)',
            shadowBlur: 10
          }
        }
      }
      return tempOption
    }
  }
}
</script>

<style>

.panel-pad {
    padding-right: 40px;
}
.widget-left {
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px;
    height: 80px;
    padding-top: 15px;
    text-align: center;
}

.widget-right {
    background: #FFFFFF none repeat scroll 0 0;
    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px;
    color: #F8F8F8;
    font-weight: 300;
    height: 80px;
    line-height: 1.6em;
    margin: 0;
    padding: 20px;
    text-align: left;
}

.widget-right .text-muted {
    color: #9fadbb;
}

.widget-right .large {
    color: #5f6468;
    font-size: 2em;
}

.panel-pink .widget-left {
    background: #875EA2 none repeat scroll 0 0;
    color: #fff;
}

.panel-yellow .widget-left {
    background: #DC69AA none repeat scroll 0 0;
    color: #fff;
}

.panel-blue .widget-left {
    background: #58B7FF none repeat scroll 0 0;
    color: #fff;
}

.panel-teal .widget-left {
    background: #1ebfae none repeat scroll 0 0;
    color: #fff;
}

.panel-orange .widget-left {
    background: #F7BA2A none repeat scroll 0 0;
    color: #fff;
}

.panel-red .widget-left {
    background: #475669 none repeat scroll 0 0;
    color: #fff;
}

.panel-widget {
    background: #fff none repeat scroll 0 0;
}

.overview-panel-widget {
    background: #F7F7FF none repeat scroll 0 0;
}

.panel .tabs {
    margin: 0;
    padding: 0;
}
.demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



