const toolsbarx = {
  state: {
    'toggleTitle': '',
    'btnGroup': [
      {fontAwesome: 'fa fa-info-circle', value: 'tools-info', toolsTip: 'cluster info'}
    ]
  },
  mutations: {
    SET_TOGGLE_TITLE: (state, title) => {
      state.toggleTitle = title
    }
  },
  actions: {
    ToggleTitle ({ commit }, index) {
      switch (index) {
        case 0:
          commit('SET_TOGGLE_TITLE', "<i class='fa fa-bar-chart-o'></i> Overview")
          break
        case 1:
          commit('SET_TOGGLE_TITLE', "<i class='fa fa-search'></i> Search")
          break
        case 2:
          commit('SET_TOGGLE_TITLE', "<i class='fa fa-list-alt'></i> Indices")
          break
        case 3:
          commit('SET_TOGGLE_TITLE', "<i class='fa fa-table'></i> Mappings")
          break
        case 4:
          commit('SET_TOGGLE_TITLE', "<i class='fa fa-gear'></i> Tools Box")
          break
        case 5:
          commit('SET_TOGGLE_TITLE', "<i class='fa fa-comments'></i> Syntactic Sugar")
          break
      }
    }
  }
}
export default toolsbarx



